.leaflet-container {
  background-color: rgba(255, 0, 0, 0);
  border: "2px solid red";
  height: 60vh;
}

#info-pane {
  font-family: "IBM Plex Sans", "sans-serif";
  background-color: white;
  position: absolute;
  top: 10px;
  right: 10px;
  z-index: 400;
  padding: 15px;
  border-radius: 10px;
  border: 2px solid #f2f4f9;
  text-align: center;
  font-size: 14px;
}

#box {
  display: flex;
  justify-content: space-evenly;
  align-content: center;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

#last-updated {
  margin-left: auto;
  margin-right: auto;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  border-radius: 5%;
  font-size: 10px;
  border: none;
  padding: 5px;
  background: white;
  text-align: center;
}

#confirmed,
#recovered,
#deaths {
  font-size: 24px;
}

#new-confirmed,
#new-recovered,
#new-deaths {
  font-size: 14px;
}

#confirmed,
#new-confirmed {
  color: orange;
  font-weight: 500;
}

#confirm,
#recovered,
#new-recovered {
  color: green;
  font-weight: 500;
}

#deaths,
#new-deaths {
  color: red;
  font-weight: 500;
}

#confirmed-card,
#recovered-card,
#deaths-card {
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-radius: 5%;
  border: none;
  padding: 12px;
  background: white;
  text-align: center;
}

#new-confirmed-box,
#new-recovered-box,
#new-deaths-box {
  display: flex;
  flex-direction: row;
  font-size: 12px;
  justify-content: center;
}

@media screen and (max-width: 600px) {
  #box {
    display: flex;
    justify-content: space-between;
    align-content: center;
    margin-top: 1rem;
    margin-bottom: 1rem;
    flex-direction: column;
  }

  #confirmed-card,
  #recovered-card,
  #deaths-card {
    margin-top: 0.25rem;
    margin-bottom: 0.25rem;
    margin-left: auto;
    margin-right: auto;
    width: 50%;
    text-align: center;
    justify-content: center;
  }
}

#difference {
  text-align: center;
}

#last-updated {
  margin-left: 0px;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  border-radius: 5%;
  font-size: 10px;
  border: none;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 0px;
  background: transparent;
  text-align: left;
  font-size: 0.8rem;
}

